import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { FilterList } from '@material-ui/icons'
import { Form } from '../../../components/useForm';
import { Grid } from '@material-ui/core';
import Controls from '../../../components/controls/Controls';


const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
    backgroundColor:"#1a1359",
    color: "#fff",
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: "#fff",
  },
  button: {
    margin: theme.spacing(0.5),
    backgroundColor: "#008647",
    color:'white',
    height: '10px',

    "&:hover":{
        backgroundColor:"#1a1359",
    }
  
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography  variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});


const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

const secteurs = [
    { id: 'informatique', title: 'Informatique' },
    { id: 'telecommunications', title: 'Télécommunications' },
    { id: 'bancaire', title: 'Bancaire' },
    { id: 'securite_gardienage', title: 'Sécurité et gardienage' },
    { id: 'tourisme', title: 'Tourisme' },
    { id: 'restauration', title: 'Restauration' },
    { id: 'hôtelerie', title: 'Hôtelerie' },
    { id: 'services_financiers', title: 'Services financiers' },
    { id: 'transport_logistique', title: 'Transport et logistique' },
    { id: 'Communication_marketing', title: 'Communication et marketing' },
    { id: 'ressources_humaines', title: 'Ressources humaines' },
    { id: 'immobilier', title: 'Immobilier' },
    { id: 'medical_sante', title: 'Médical et santé' },
    { id: 'commerce', title: 'Commerce' },
    { id: 'service_publique', title: 'Service Publique' },
    { id: 'formation', title: 'Formation' },
    { id: 'garde_enfant', title: 'Garde enfants' },  
]

const contrats = [
    { id: 'CDD', title: 'CDD' },
    { id: 'CDI', title: 'CDI' },
    { id: 'stage', title: 'Stage' },
    { id: 'freelance', title: 'Freelance' }
]

const frequences = [
    { id: 'temps_plein', title: 'Temps plein' },
    { id: 'temps_partiel', title: 'Temps partiel' },
]


const initialValues = {

    secteurActivite:'',
    typeContrat:'',
    frequence:'',

}

export default function JobFilter(props) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button 
            variant="contained"  
            onClick={handleClickOpen}
            style={{color:"white", backgroundColor:"#1a1359"}}
            startIcon={<FilterList/>}
           size="medium"
        >
        Filtre
      </Button>
      <Dialog onClose={props.handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          Filtre
        </DialogTitle>
        <DialogContent dividers>
              <Form>
                    <Grid  container spacing={3} lg={12} md={12}  xs={12}>
                        <Grid item lg={6} md={6} xs={12}>
                            <Controls.RadioGroup 
                                    name="secteurActivite"
                                    label="Secteurs d'activés"
                                    value={initialValues.secteurActivite}
                                    items={secteurs}
                            />
                        </Grid>
                        <Grid item lg={6} md={6} xs={12}>
                           <Controls.RadioGroup 
                                    name="typeContrat"
                                    label="Type de contrat"
                                    value={initialValues.typeContrat}
                                    items={contrats}
                            />
                             <Controls.RadioGroup 
                                    name="frequences"
                                    label="Fréquence"
                                    value={initialValues.frequence}
                                    items={frequences}
                            />
                        </Grid>
                    </Grid> 
              </Form> 
        </DialogContent>
        <DialogActions>
          
        </DialogActions>
      </Dialog>
    </div>
  );
}
