import React, {useState} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import Sidebar from '../components/Publicite/Sidebar'
import { Container,makeStyles, Grid, Typography, Box } from '@material-ui/core'
import {dataConseil} from '../components/data/dataConseil'
import {jobData} from '../components/data/jobData'
import ViewJobModal from '../components/Job/ViewJobModal'
import NavbarSearch from '../components/NavbarSearch'
import CardOne from '../components/Cards/CardOne'
import SearchBar from '../components/SearchBar/SearchBar'
import JobFilter from '../components/Job/JobFilter'
import Job from '../components/Job/Job'
import CardPub from '../components/Publicite/CardPub'





const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`
const PageContent = styled.div`   
   padding-top: 225px;
   margin-left:30px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const DivSearch = styled.div`   
   display:none;
   width: 100%;

   @media screen and (max-width: 1025px){
       display:block;
    }
  
`
const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    },
    button: {
        margin: theme.spacing(1),
        backgroundColor: "#008647",
        color:'white',

        "&:hover":{
            backgroundColor:"#1a1359",
        }
      
      },
    barFilter:{
        justifyContent:'space-between'
    }
  }));

const Emplois = () => {

    const [viewJob, setViewJob] = useState(false)

    const classes = useStyles();

    const [isOpen, setIsOpen] = useState(false)


    const toggle = () =>{
        setIsOpen(!isOpen)
    }
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <NavbarSearch  toggle={toggle}/>
               {/* <PubLaterale   toggle={toggle}/>*/}
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container >
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>

                                        <DivSearch>
                                                <Grid xs={12} >
                                                    <SearchBar/>
                                                </Grid>
                                        </DivSearch>

                                    <Grid item   lg={8} md={6} xs={12}>
                                      <Box>
                                          <Box  className={classes.barFilter} display="flex" mb={1}>
                                           <Typography variant="h6" component="h2">Offres d'emploi:  6 résultats</Typography>
                                            
                                            <JobFilter   />
                                          </Box>
                                        </Box>
                                    
                                        <ViewJobModal  job={viewJob}  closeModal={()=> setViewJob({})}/>
                                          {jobData.map(job => 
                                                <Job  open={()=>setViewJob(job)} key={job.id} {...job} />                
                                            )
                                          }
                                    </Grid>
                                      
                                        {dataConseil.slice(0,1).map(conseil => 
                                           <Grid item lg={4} md={6}  xs={12}  key={conseil.id}>
                                               <CardPub/>
                                              <CardOne  {...conseil}  />
                                              
                                            </Grid>
                                        )}

                                  
                            </Grid>
                    </Container>
            </PageContent>
            <Footer/>
            
        </>
    )
}

export default Emplois
