import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'



const initialValues = {
    id:0,
    interet: '',
}

const Interets = (props) => {
   
    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('langue' in fieldValues)
            temp.langue = fieldValues.langue.length !== 0 ? "" : "Champ requis."
        if ('niveau' in fieldValues)
            temp.niveau = fieldValues.niveau.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    title="Intérets"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container
                    >
                       <Grid item md={6} xs={6}>
                                <Controls.Input
                                        name="interets"
                                        label="Intérets"
                                        value={values.autreCompetence}
                                        onChange={handleInputChange}
                                    />
                        </Grid>
                       
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Valider" />
                </Box>
            </Card>
        </Form>
    )
}

export default Interets
