import React, {useState} from 'react'
import { Link as RouterLink } from 'react-router-dom';
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Link,
    TextField,
    Typography,
    Button,
    Checkbox,
    FormHelperText,
} from '@material-ui/core'
import { Formik } from 'formik';
import * as Yup from 'yup';
import {GetProvinces, GetVilles} from '../data/administrative'
import ConfirmAuth from '../ConfirmAuth'

const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.dark,
      height: '100%',
      paddingBottom: theme.spacing(3),
      paddingTop: theme.spacing(3)
    },
    textPrimary:{
        color: '#008647',
      },

      buttonStyle: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#008649",
        }
    }
  }));

const RegisterForm = () => {

    const classes = useStyles();
    const [confirmAuth, setConfirmAuth] = useState({ isOpen: false, title: '', subTitle: '' })


    return (
        <>
            <Paper className={classes.root}>
            <Box
                display="flex"
                flexDirection="column"
                height="100%"
                justifyContent="center"
            >
                <Container maxWidth="sm">
                <Formik
                    initialValues={{
                    email: '',
                    nomSociete: '',
                    password: '',
                    adresse: '',
                    referent:'',
                    ville:'',
                    province:'',
                    raison_sociale:'',
                    activite_principale: '',
                    policy: false
                    }}
                    validationSchema={
                    Yup.object().shape({
                        email: Yup.string().email('Ajouter un email valide').max(255).required('Email de la société requis'),
                        nomSociete: Yup.string().max(255).required('Le nom de votre société est requis'),
                        adresse: Yup.string().max(255).required('Votre adresse est requise'),
                        referent: Yup.string().max(255).required('Votre reférent est requis'),
                        password: Yup.string().max(255).required('Votre mot de passe est requis'),
                        activite_principale: Yup.string().max(255).required('Champ requis'),
                        raison_sociale: Yup.string().max(255).required('Raison sociale requise'),
                        policy: Yup.boolean().oneOf([true], 'Vous devez acceptez les termes et conditions')
                    })
                    }
                    onSubmit={() => {
                        setConfirmAuth({
                            isOpen: true,
                            title: 'Votre inscription a été faite avec succès!',
                            subTitle: "Consulter votre boîte mail, pour confirmer votre compte",
                        })
                    }}
                >
                    {({
                    errors,
                    handleBlur,
                    handleChange,
                    handleSubmit,
                    isSubmitting,
                    touched,
                    values
                    }) => (
                    <form onSubmit={handleSubmit}>
                        <Box mb={3}>
                        <Typography
                            color="textPrimary"
                            variant="h4"
                        >
                            Créer un nouveau compte
                        </Typography>
                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="body2"
                        >
                            Utiliser votre email pour créer un compte
                        </Typography>
                        </Box>
                    
                    <Grid
                        container
                        spacing={2}
                    >
                     <Grid item md={6}   xs={12}  >
                        <TextField
                        error={Boolean(touched.nomSociete && errors.nomSociete)}
                        fullWidth
                        helperText={touched.nomSociete && errors.nomSociete}
                        label="Nom de la société"
                        margin="normal"
                        name="nomSociete"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.nomSociete}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.adresse && errors.adresse)}
                        fullWidth
                        helperText={touched.adresse && errors.adresse}
                        label="Adresse"
                        margin="normal"
                        name="adresse"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.adresse}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6}  xs={12}>
                    <TextField
                        fullWidth
                        label="Ville"
                        name="ville"
                        onChange={handleChange}
                        required
                        select
                        SelectProps={{ native: true }}
                        value={values.GetVilles}
                        variant="outlined"
                    >
                        {GetVilles.map((option) => (
                        <option
                            key={option.value}
                            value={option.value}
                        >
                            {option.label}
                        </option>
                        ))}
                    </TextField>
                    </Grid>
                    <Grid item md={6}  xs={12}   >
                    <TextField
                        fullWidth
                        label="Province"
                        name="province"
                        onChange={handleChange}
                        required
                        select
                        SelectProps={{ native: true }}
                        value={values.GetProvinces}
                        variant="outlined"
                    >
                        {GetProvinces.map((option) => (
                        <option
                            key={option.value}
                            value={option.value}
                        >
                            {option.label}
                        </option>
                        ))}
                    </TextField>
                    </Grid>
                    <Grid item md={6}  xs={12}>
                        <TextField
                        error={Boolean(touched.email && errors.email)}
                        fullWidth
                        helperText={touched.email && errors.email}
                        label="Email"
                        margin="normal"
                        name="email"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        type="email"
                        value={values.email}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.activite_principale && errors.activite_principale)}
                        fullWidth
                        helperText={touched.activite_principale && errors.activite_principale}
                        label="Activité principale"
                        margin="normal"
                        name="activite_principale"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.activite_principale}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.raison_sociale && errors.raison_sociale)}
                        fullWidth
                        helperText={touched.raison_sociale && errors.raison_sociale}
                        label="Raison sociale"
                        margin="normal"
                        name="raison_sociale"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.raison_sociale}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.referent && errors.referent)}
                        fullWidth
                        helperText={touched.referent && errors.referent}
                        label="Reférent"
                        margin="normal"
                        name="referent"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.referent}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item   md={6} xs={12}  >
                        <TextField
                        error={Boolean(touched.password && errors.password)}
                        fullWidth
                        helperText={touched.password && errors.password}
                        label="Mot de passe"
                        margin="normal"
                        name="password"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        type="password"
                        value={values.password}
                        variant="outlined"
                        />
                     </Grid>
                     <Grid item   md={6} xs={12}  >
                        <TextField
                        error={Boolean(touched.password && errors.password)}
                        fullWidth
                        helperText={touched.password && errors.password}
                        label="Confirmer le mot de passe"
                        margin="normal"
                        name="password"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        type="password"
                        value={values.password}
                        variant="outlined"
                        />
                     </Grid>

                        </Grid>
                        <Box
                        alignItems="center"
                        display="flex"
                        ml={-1}
                        >
                        <Checkbox
                            checked={values.policy}
                            name="policy"
                            onChange={handleChange}
                        />
                        <Typography
                            color="textSecondary"
                            variant="body1"
                        >
                            J'ai lu les
                            {' '}
                            <Link
                            color="primary"
                            component={RouterLink}
                            to="#"
                            underline="always"
                            variant="h6"
                            className={classes.textPrimary}
                            >
                            Termes et Conditions
                            </Link>
                        </Typography>
                        </Box>
                        {Boolean(touched.policy && errors.policy) && (
                        <FormHelperText error>
                            {errors.policy}
                        </FormHelperText>
                        )}
                        <Box my={2}>
                        <Button
                            className={classes.buttonStyle}
                            disabled={isSubmitting}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                        >
                            S&apos;inscrire maintenant
                        </Button>
                        </Box>
                        <Typography
                        color="textSecondary"
                        variant="body1"
                        >
                        Vous avez un compte?
                        {' '}
                        <Link
                            component={RouterLink}
                            to="/recruteur/connexion"
                            variant="h6"
                            className={classes.textPrimary}
                        >
                            Se connecter
                        </Link>
                        </Typography>
                    </form>
                    )}
                </Formik>
                </Container>
            </Box>
            <ConfirmAuth
                confirmAuth={confirmAuth}
                setConfirmAuth={setConfirmAuth}
             />
            </Paper>
        </>
    )
}

export default RegisterForm
