import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'



const initialValues = {
    id:0,
    langage: '',
    niveauLangage:  '',
    AnneExperienceLangage: '',
    bibliotheque: '',
    niveauBibliotheque:'',
    AnneExperienceBibliotheque: '',
   
}

const FormInformatique = (props) => {
   
    const {addOrEdit, recordForEdit} = props
    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('langue' in fieldValues)
            temp.langue = fieldValues.langue.length !== 0 ? "" : "Champ requis."
        if ('niveau' in fieldValues)
            temp.niveau = fieldValues.niveau.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }


    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true,validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card t={2}>
                <CardHeader
                    title="Langages informatiques"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container  
                    >
                       <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="langage"
                                    label="Langage"
                                    value={values.langage}
                                    onChange={handleInputChange}
                                    options={collection.getLangageInformatique()}
                                />
                                </Grid>
                            <Grid item md={6} xs={12}>
                                    <Controls.Select
                                        name="niveauLangage"
                                        label="Niveau"
                                        value={values.niveauLangage}
                                        onChange={handleInputChange}
                                        options={collection.getNiveauCollection()}
                                    />
                            </Grid>
                            <Grid item md={6} xs={12}>
                                <Controls.Input
                                        name="AnneExperienceLangage"
                                        label="Année(s) d'experience"
                                        type="number"
                                        value={values.AnneExperienceBibliotheque}
                                        onChange={handleInputChange}
                                    
                                    />
                            </Grid>
                      </Grid>
                      </CardContent>
                      </Card>

                        <Divider/>
                        <Card>
                        <CardHeader
                            title="Bibliothèques et Framworks"
                       />
                        <Divider/>
                        <CardContent>
                        <Grid  container >
                            <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="bibliotheque"
                                    label="bibliotheque & Framwork "
                                    value={values.bibliotheque}
                                    onChange={handleInputChange}
                                    options={collection.getBibliotheque()}
                                />
                                </Grid>
                            <Grid item md={6} xs={12}>
                                    <Controls.Select
                                        name="niveauBibliotheque"
                                        label="Nieau"
                                        value={values.niveauBibliotheque}
                                        onChange={handleInputChange}
                                        options={collection.getNiveauCollection()}
                                    />
                            </Grid>
                            <Grid item md={6} xs={12}>
                                <Controls.Input
                                        name="AnneExperienceBibliotheque"
                                        label="Année(s) d'experience"
                                        type="number"
                                        value={values.AnneExperienceBibliotheque}
                                        onChange={handleInputChange}
                                    
                                    />
                            </Grid>
                            </Grid>
                            </CardContent>
                            <Divider />
                            <Box
                                display="flex"
                                justifyContent="flex-end"
                                p={2}
                            >
                            <Controls.Button
                                        type="submit"
                                        text="Valider" />
                            </Box>
                        </Card>
                       
        </Form>
    )
}

export default FormInformatique
