import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'


const initialValues = {
    nom: 'Lorem',
    prenom:'Jean Pierre',
    email: 'jeanpierre@gmail.com',
    telephone: '00241000000',
    ville:'Libreville',
    provinceId: 'Estuaire',
    situation: 'Chomeur',
    image: ''
}

export const getProvinceCollection = ()=>([
    { id: '1', title: 'Estuaire' },
    { id: '2', title: 'Haut-Ogooue' },
    { id: '3', title: 'Moyen-Ogooue' },
    { id: '4', title: 'Ngounie' },
    { id: '5', title: 'Nyanga' },
    { id: '6', title: 'Ogooue-Ivindo' },
    { id: '7', title: 'Ogooue-Lolo' },
    { id: '8', title: 'Ogooue-Maritime' },
    { id: '9', title: 'Woleu-Ntem' }
  
])
  

const ProfilForm = (props) => {
   
    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom' in fieldValues)
            temp.nom = fieldValues.nom ? "" : "Champ requis."
        if ('prenom' in fieldValues)
            temp.prenom = fieldValues.prenom ? "" : "Champ requis."
        if ('email' in fieldValues)
            temp.email = (/$^|.+@.+..+/).test(fieldValues.email) ? "" : "Email non valide."
        if ('telephone' in fieldValues)
            temp.telephone = fieldValues.telephone.length > 8 ? "" : "Minimum de 9 nombre requis."
        if ('ville' in fieldValues)
            temp.ville = fieldValues.ville.length !== 0 ? "" : "Champ requis."
        if ('situation' in fieldValues)
            temp.situation = fieldValues.situation.length !== 0 ? "" : "Champ requis."
        if ('provinceId' in fieldValues)
            temp.provinceId = fieldValues.provinceId.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    subheader="Vous pouvez éditer vos informations"
                    title="Profil"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container
                        spacing={3}
                    >
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="nom"
                                    label="Nom"
                                    value={values.nom}
                                    onChange={handleInputChange}
                                    error={errors.nom}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="prenom"
                                    label="prenom"
                                    value={values.prenom}
                                    onChange={handleInputChange}
                                    error={errors.prenom}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="provinceId"
                                    label="Province"
                                    value={values.provinceId}
                                    onChange={handleInputChange}
                                    options={getProvinceCollection()}
                                    error={errors.provinceId}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="ville"
                                    label="ville"
                                    value={values.ville}
                                    onChange={handleInputChange}
                                    error={errors.ville}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="telephone"
                                    label="telephone"
                                    value={values.telephone}
                                    onChange={handleInputChange}
                                    error={errors.telephone}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="email"
                                    label="email"
                                    value={values.email}
                                    onChange={handleInputChange}
                                    error={errors.email}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="situation"
                                    label="Situation"
                                    value={values.situation}
                                    onChange={handleInputChange}
                                    error={errors.situation}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    type="file"
                                    name="image"
                                    label="Image"
                                   
                                    value={values.image}
                                    onChange={handleInputChange}
                                    error={errors.image}
                                />
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Valider" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                        onClick={resetForm} />
                </Box>
            </Card>
        </Form>
    )
}

export default ProfilForm
