import React, {useState, useEffect} from 'react'
import {
    Nav, 
    NavbarContainer, 
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLink,
    NavBtn,
    NavBtnLink       
} from './NavbarStyle'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'

const Navbar = ({toggle}) => {
    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => { 
        if(window.scrollY >= 80) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }

    useEffect(()=> {
        window.addEventListener('scroll', changeNav)
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav} >
                <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome} >
                            JOBSGABON
                        </NavLogo>
                        <MobileIcon  onClick={toggle} >
                            <FaBars />
                        </MobileIcon>
                        <NavMenu> 
                        <NavItem>
                                <NavLink 
                                    to="about"
                                    smooth={true}
                                    duration={500} 
                                    spy={true}
                                    exact={true} 
                                    offset={-80}
                                    
                                >
                                    À propos
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink 
                                to="produits"
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}>
                                    Nos produits
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink 
                                to="tarifs"
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}
                                
                                >Nos Tarifs</NavLink>
                            </NavItem>
                           
                        </NavMenu>
                        <NavBtn>
                            <NavBtnLink to="/recruteur/connexion" scrollNav={scrollNav}>Se connecter</NavBtnLink>
                            <NavBtnLink to="/recruteur/inscription" scrollNav={scrollNav}>S'inscrire</NavBtnLink>
                        </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar
