import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
        makeStyles
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'



const initialValues = {
    id:0,
    intitule: '',
    langage1:  '',
    langage2:  '',
    langage3:  '',
    bibliotheque1:'',
    bibliotheque2:'',
    description: '',
    image:''
}

const useStyles = makeStyles((theme)=> ({
    root:{
        backgroundColor: '#008647'
    },
}));


const Formproject = (props) => {
    const classes = useStyles();
   
    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('intitule' in fieldValues)
            temp.intitule = fieldValues.intitule ? "" : "Champ requis."
        if ('discipline' in fieldValues)
            temp.discipline = fieldValues.discipline ? "" : "Champ requis."
        if ('niveauAtteint' in fieldValues)
            temp.niveauAtteint = fieldValues.niveauAtteint ? "" : "Champ requis."
        if ('description' in fieldValues)
            temp.description = fieldValues.description ? "" : "Champ requis."
        if ('province' in fieldValues)
            temp.province = fieldValues.province.length !== 0 ? "" : "Champ requis."
        if ('ville' in fieldValues)
            temp.ville = fieldValues.ville.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card t={2}>
                <CardHeader
                    title="Vos Projets"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container  
                    >
                       <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="intitule"
                                    label="Intitulé du projet"
                                    value={values.intitule}
                                    onChange={handleInputChange}
                                />
                        </Grid>

                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="langage1"
                                    label="langage 1"
                                    value={values.langage}
                                    onChange={handleInputChange}
                                    options={collection.getLangageInformatique()}  
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="langage2"
                                    label="langage 2"
                                    value={values.langage2}
                                    onChange={handleInputChange}
                                    options={collection.getLangageInformatique()}  
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="langage3"
                                    label="langage 3"
                                    value={values.langage3}
                                    onChange={handleInputChange}
                                    options={collection.getLangageInformatique()}  
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="bibliotheque1"
                                    label="bibliotheque 1"
                                    value={values.bibliotheque1}
                                    onChange={handleInputChange}
                                    options={collection.getBibliotheque()}  
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="bibliotheque2"
                                    label="bibliotheque 2"
                                    value={values.bibliotheque2}
                                    onChange={handleInputChange}
                                    options={collection.getBibliotheque()}  
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                              <Controls.InputMultiligne
                                    name="description"
                                    label="Description"
                                    rows="4"
                                    value={values.description}
                                    onChange={handleInputChange}
                                    
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    type="file"
                                    name="image"
                                    value={values.image}
                                    onChange={handleInputChange}
                                />
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Valider" />
                </Box>
            </Card>
        </Form>
    )
}

export default Formproject
