import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'



const initialValues = {
    id:0,
    intitule: '',
    dateDebut:  new Date(),
    dateFin:  new Date(),
    discipline: '',
    niveauAtteint: '',
    province: '',
    ville: '',
}

const FormationForm = (props) => {
   
    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('intitule' in fieldValues)
            temp.intitule = fieldValues.intitule ? "" : "Champ requis."
        if ('discipline' in fieldValues)
            temp.discipline = fieldValues.discipline ? "" : "Champ requis."
        if ('niveauAtteint' in fieldValues)
            temp.niveauAtteint = fieldValues.niveauAtteint ? "" : "Champ requis."
        if ('description' in fieldValues)
            temp.description = fieldValues.description ? "" : "Champ requis."
        if ('province' in fieldValues)
            temp.province = fieldValues.province.length !== 0 ? "" : "Champ requis."
        if ('ville' in fieldValues)
            temp.ville = fieldValues.ville.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    title="Vos Formations"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container  
                    >
                       <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="intitule"
                                    label="Intitulé de la formation"
                                    value={values.intitule}
                                    onChange={handleInputChange}
                                    error={errors.intitule}
                                />
                        </Grid>

                        <Grid item md={6} xs={12}>
                            <Controls.DatePicker
                                name="dateDebut"
                                label="Date de début"
                                value={values.dateDebut}
                                onChange={handleInputChange}
                           />
                        </Grid>
                        <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="discipline"
                                    label="Discipline"
                                    value={values.fonction}
                                    onChange={handleInputChange}
                                    error={errors.discipline}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <Controls.DatePicker
                                name="dateFin"
                                label="Date de Fin"
                                value={values.dateFin}
                                onChange={handleInputChange}
                           />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="niveauAtteint"
                                    label="Niveau Atteint"
                                    value={values.niveauAtteint}
                                    onChange={handleInputChange}
                                    options={collection.getNiveauFormation()}
                                    error={errors.niveauAtteint}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="province"
                                    label="Province"
                                    value={values.province}
                                    onChange={handleInputChange}
                                    options={collection.getProvinceCollection()}
                                    error={errors.province}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="ville"
                                    label="Ville"
                                    value={values.ville}
                                    onChange={handleInputChange}
                                    options={collection.getVilleCollection()}
                                    error={errors.ville}
                                />
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Valider" />
                </Box>
            </Card>
        </Form>
    )
}

export default FormationForm
