import React from 'react'
import { TextField } from '@material-ui/core';

export default function Input(props) {

    const { name,type, label, value,error=null, onChange ,size } = props;
    return (
        <TextField
            style={{backgroundColor: '#fff',maxWidth: '90%'}}
            variant="outlined"
            label={label} 
            size={size}
            fullWidth
            type={type}
            name={name}
            value={value}
            onChange={onChange}
            {...(error && {error:true,helperText:error})}
        /> 
    )
}
