import React from 'react'
import { 
    Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import { getProvinceCollection } from '../data/collection'



const initialValues = {
    id:0,
    intitule: '',
    dateDebut:  new Date(),
    dateFin:  new Date(),
    fonction: '',
    secteuractivite: '',
    province: '',
    description: '',
}

const FormExp = (props) => {
   
    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('intitule' in fieldValues)
            temp.intitule = fieldValues.intitule ? "" : "Champ requis."
        if ('fonction' in fieldValues)
            temp.fonction = fieldValues.fonction ? "" : "Champ requis."
        if ('secteuractivite' in fieldValues)
            temp.secteuractivite = fieldValues.secteuractivite ? "" : "Champ requis."
        if ('description' in fieldValues)
            temp.description = fieldValues.description ? "" : "Champ requis."
        if ('province' in fieldValues)
            temp.province = fieldValues.province.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    title="Vos Expériences"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container  
                    >
                       <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="intitule"
                                    label="Intitulé de la formation"
                                    value={values.intitule}
                                    onChange={handleInputChange}
                                    error={errors.intitule}
                                />
                        </Grid>

                        <Grid item md={6} xs={12}>
                            <Controls.DatePicker
                                name="dateDebut"
                                label="Date de début"
                                value={values.dateDebut}
                                onChange={handleInputChange}
                           />
                        </Grid>
                        <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="fonction"
                                    label="Fonction"
                                    value={values.fonction}
                                    onChange={handleInputChange}
                                    error={errors.fonction}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <Controls.DatePicker
                                name="dateFin"
                                label="Date de Fin"
                                value={values.dateFin}
                                onChange={handleInputChange}
                           />
                        </Grid>
                        <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="secteuractivite"
                                    label="Secteur d'activité"
                                    value={values.secteuractivite}
                                    onChange={handleInputChange}
                                    error={errors.secteuractivite}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Select
                                    name="province"
                                    label="Province"
                                    value={values.province}
                                    onChange={handleInputChange}
                                    options={getProvinceCollection()}
                                    error={errors.province}
                                />
                        </Grid>
                        <Grid item md={8} xs={12}>
                              <Controls.InputMultiligne
                                    name="description"
                                    label="Description"
                                    rows="4"
                                    value={values.description}
                                    onChange={handleInputChange}
                                    error={errors.description}
                                />
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Valider" />
                </Box>
            </Card>
        </Form>
    )
}

export default FormExp
