import React from 'react'
import styled from 'styled-components'



const H3 = styled.h3`
      font-size: 1rem;
      margin-bottom: 1rem;
      color: #1a1359;
      text-transform: uppercase;
      margin-bottom: 0.3rem;

      @media screen and (max-width: 950px) {
            font-size: 0.8rem !important;
      }
`
const Progress = styled.div`
    height: .4rem;
    background: transparent;
    border-radius: 50px;
    margin-top: 0.1rem;
    position: relative;

    &:after {
        content: '';
        position: absolute;
        background: #008647;
        border-radius: 50px;
        height: .2rem;
        width: 0;
        animation: line 2s ease-out forwards;

        @keyframes line {
            to {
                width: 100%;
            }
        }
    }

`

const Years = styled.div`
        font-size: 10px;
        display: flex;
        justify-content: space-between;
        color: #696d6b;
`

const Span = styled.span`
      width: 36%;

      &:nth-child(2){
            text-align: center; 
            width: 20%;
          }
      &:nth-child(3){
            text-align: right;
            width: 44%;
        }

        &:nth-child(2):before,
          &:nth-child(3):before {
            content:'';
            background: #ededee;
            position: absolute;
            height: 146px;
            width: 1px;
            transform: translate(9px, 19px);
          }
`

const ProgressBar = (props) => {
   
    return (
        <div className={props.className}>
                <H3>{props.title}</H3>
                <Years>
                    <Span>Années d'expérience</Span>
                    <Span>1 an</Span>
                    <Span>2 ans</Span>
                </Years>

                <div>
                    {
                        props.languages.map((item) => {
                            let xpYears = 2;
                            let progressBar = item.xp / xpYears * 100 + '%';

                            return (
                                <div key={item.id} className="languagesList">
                                    <li>{item.value}</li>
                                    <Progress className="progresBar"  style={
                                        {width:progressBar}}></Progress>
                                </div>
                            )
                        }) 
                    }
                </div>
        </div>
    )
}

export default ProgressBar
