import React from 'react';
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'

import Accueil from './pages/Accueil';

import ProfilEntreprise from './pages/ProfilEntreprise';
import Recruteur from './recruteurs/pages/Recruteur';
import Annonces from './recruteurs/pages/Annonces';
import Produits from './recruteurs/pages/Produits';
import Tarifs from './recruteurs/pages/Tarifs';
import LoginR from './recruteurs/pages/LoginR';
import RegisterR from './recruteurs/pages/RegisterR';


import CvTheque from './recruteurs/pages/CvTheque';
import Candidat from './candidats/pages/Candidat';
import Emplois from './candidats/pages/Emplois';
import Conseils from './candidats/pages/Conseils';
import Actualites from './candidats/pages/Actualites';
import Login from './candidats/pages/Login';
import Register from './candidats/pages/Register';

import Signin from './pages/Signin';
 
import Contact from './candidats/pages/Contact'
import HomeCandidat from './candidats/pages/HomeCandidat'
import Knowledges from './candidats/pages/Knowledges'
import MesOffres from './candidats/pages/MesOffres'
import Portefolio from './candidats/pages/Portefolio'
import MonCompte from './candidats/pages/MonCompte'
import EditCv from './candidats/pages/EditCv'



function App() {
  return (
    <>
   { /*basename={process.env.PUBLIC_URL}
      "homepage": "https://hanseroland.github.io/jobsgabon/",
      "predeploy": "npm run build",
      "deploy": "gh-pages -d build"
   
   */}
        <Router basename={process.env.PUBLIC_URL}  >
              <Switch>
                  <Route  exact path="/" component={Accueil} />
                  <Route   path="/profil_entreprise" component={ProfilEntreprise} />
                  <Route   path="/connexion" component={Signin} />

                
                  <Route  exact path="/recruteur" component={Recruteur} />
                  <Route   path="/recruteur/annonces" component={Annonces} />   
                  <Route   path="/recruteur/produits" component={Produits} /> 
                  <Route   path="/recruteur/tarifs" component={Tarifs} />  
                  <Route   path="/recruteur/cvtheque" component={CvTheque} /> 
                  <Route   path="/recruteur/connexion" component={LoginR} /> 
                  <Route   path="/recruteur/inscription" component={RegisterR} /> 
                 

                  <Route  exact path="/candidats" component={Candidat} /> 
                  <Route   path="/candidats/emplois" component={Emplois} /> 
                  <Route   path="/candidats/conseils" component={Conseils} /> 
                  <Route   path="/candidats/actualites" component={Actualites} /> 
                  <Route   path="/candidats/connexion" component={Login} /> 
                  <Route   path="/candidats/inscription" component={Register} /> 

                  <Route  exact path="/candidats/accueil" component={HomeCandidat} />
                  <Route   path="/candidats/competences" component={Knowledges} />
                  <Route   path="/candidats/portefolio" component={Portefolio} />
                  <Route   path="/candidats/contact" component={Contact} />
                  <Route   path="/candidats/mescandidatures" component={MesOffres} />
                  <Route   path="/candidats/moncompte" component={MonCompte} />
                  <Route   path="/candidats/editercv" component={EditCv} />
                          
              </Switch>
        </Router>
    </>
  );
}

export default App;
