import React, {useState, useEffect} from 'react'
import {
    Nav, 
    NavbarContainer, 
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLink,
    NavBtn,
    NavBtnLink,
           
} from './NavbarElements'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'

const NavbarHome = ({toggle}) => {
    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => { 
        if(window.scrollY >= 80) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }

    useEffect(()=> {
        window.addEventListener('scroll', changeNav)
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav} >
                <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome} scrollNav={scrollNav} >
                            JOBSGABON
                        </NavLogo>
                        <MobileIcon  onClick={toggle} >
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>
                        {/*<NavItem>
                                <NavLink scrollNav={scrollNav}
                                    to="services"
                                    smooth={true}
                                    duration={500} 
                                    spy={true}
                                    exact={true} 
                                    offset={-80}
                                    
                                >
                                    Nos services
                                </NavLink>
                        </NavItem>*/}
                          <NavItem>
                                <NavLink scrollNav={scrollNav}
                                to="candidats"
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}
                                
                                >Candidats</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink scrollNav={scrollNav}
                                to="recruteurs"
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}
                                
                                >Recruteurs</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink scrollNav={scrollNav}
                                to="offres"
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}>
                                   Offres d'emploi
                                </NavLink>
                            </NavItem>
                           
                           
                        </NavMenu>
                        <NavBtn>
                            <NavBtnLink scrollNav={scrollNav} to="/connexion" >Se connecter</NavBtnLink>
                        </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default NavbarHome
