import React from 'react'
import { Dialog, DialogTitle, DialogContent,Button, DialogActions, Typography, makeStyles, IconButton } from '@material-ui/core'
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';

const useStyles = makeStyles(theme => ({
    dialog: {
        padding: theme.spacing(2),
        position: 'absolute',
        top: theme.spacing(5)
    },
    dialogTitle: {
        textAlign: 'center'
    },
    dialogContent: {
        textAlign: 'center'
    },
    dialogAction: {
        justifyContent: 'center'
    },
    titleIcon: {
        color: '#008647',
        '&:hover': {
           
            cursor: 'default'
        },
        '& .MuiSvgIcon-root': {
            fontSize: '8rem',
        }
    },
    buttonStyle:{
        color: '#fff',
        backgroundColor:'#008647',
        '&:hover': {
           
            backgroundColor:'#008647',
        },
    }
}))

export default function ConfirmAuth(props) {

    const { confirmAuth, setConfirmAuth } = props;
    const classes = useStyles()

    return (
        <Dialog open={confirmAuth.isOpen} classes={{ paper: classes.dialog }}>
            <DialogTitle className={classes.dialogTitle}>
                <IconButton disableRipple className={classes.titleIcon}>
                    <ThumbUpAltOutlinedIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Typography variant="h6">
                    {confirmAuth.title}
                </Typography>
                <Typography variant="subtitle2">
                    {confirmAuth.subTitle}
                </Typography>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                    <Button className={classes.buttonStyle}
                            
                            size="small"
                            variant="contained"
                            onClick={() => setConfirmAuth({ ...confirmAuth, isOpen: false })}
                        >
                            Fermer
                        </Button>
            </DialogActions>
        </Dialog>
    )
}
