import React, { useState }  from 'react'
import { 
     Container,
     Grid, 
     makeStyles, 
     Paper, 
     TableBody, 
     TableRow, 
     TableCell, 
     Toolbar, 
     InputAdornment 

} from '@material-ui/core'
import styled from 'styled-components'
import Sidebar from '../components/Sidebar/Index'
import useTable from "../components/useTable";
import { Search } from "@material-ui/icons";
import Controls from "../components/controls/Controls";
import dataCandidatures from "../components/data/dataCandidatures.json"
import '../components/Candidatures/Candidatures.css'
import ScrollToTop from '../../components/Signin/ScrollToTop'







const CandidatureSection = styled.div`
    display:flex;
    @media screen and (max-width: 850px){
        display:block;
    } 
`

const CandidatureContent = styled.div`

    margin-left: 10px;
    padding: 5px;
    background-color: #F8F8F8;

    @media screen and (max-width: 850px){
        margin-left: 0 !important;
    }

`

const headCells = [
    { id: 'entreprise', label: 'Entrprise' },
    { id: 'secteurActivite', label: "Secteur d'activité" },
    { id: 'typeContrat', label: 'Type de Contrat' },
    { id: 'dateParution', label: 'Date de parution' },
    { id: 'datePostulation', label: 'Date de postulation' },
]

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const MesOffres = () => {

    const classes = useStyles();
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState(dataCandidatures)


    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.entreprise.toLowerCase().includes(target.value))
            }
        })
    }

    return (
        <>
        <ScrollToTop/>
        <CandidatureSection>
            <Sidebar/>
            <CandidatureContent>
             <Container maxWidth="lg">
                    <Grid container>
                            <Grid item  md={12} xs={12}>
                            <Paper className={classes.pageContent}>
                            <Grid item  md={4} xs={12}>
                            <Toolbar>
                                <Controls.Input
                                    label="Rechercher par entreprise"
                                    className={classes.searchInput}
                                    InputProps={{
                                        startAdornment: (<InputAdornment position="start">
                                            <Search />
                                        </InputAdornment>)
                                    }}
                                    onChange={handleSearch}
                                />
                            </Toolbar>
                            </Grid>

                             <Grid className="container">
                            <TblContainer>
                                <TblHead />
                                <TableBody>
                                    {
                                        recordsAfterPagingAndSorting().map(item =>
                                            (<TableRow key={item.id}>
                                                <TableCell>{item.entreprise}</TableCell>
                                                <TableCell>{item.secteurActivite}</TableCell>
                                                <TableCell>{item.typeContrat}</TableCell>
                                                <TableCell>{item.dateParution}</TableCell>
                                                <TableCell>{item.datePostulation}</TableCell>
                                            </TableRow>)
                                        )
                                    }
                                </TableBody>
                            </TblContainer>
                            <TblPagination />
                            </Grid>
                            </Paper>
                            </Grid>
                    </Grid>
             </Container>
            </CandidatureContent>
        </CandidatureSection>
        </>
    )
}

export default MesOffres
